import { Component, OnInit } from '@angular/core';
import { ApiService } from './shared/api.service';
import { IUnsplashImage } from './shared/unsplash-image';
import { LocationService } from './shared/location.service';
import { LocationObject } from './shared/location-object.model';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'mudita-client';
  imageJsons: IUnsplashImage[] = new Array<IUnsplashImage>();
  private interval;

  constructor(private apiService: ApiService, private locationService: LocationService) {
    this.locationCount = 0;
    this.locations = new Array<LocationObject>();
    this.locations.push(new LocationObject());
  }

  locations: Array<LocationObject>;
  locationCount: number;

  distanceFromMe: number;

  ngOnInit() {
    this.getCurrentLocation();
    //this.start();
    //this.getImage();
  }

  start() {
    this.interval = setInterval(() => {
      this.frame();
    }, 50 * 60);
  }

  onSelectLocation(event){
    let location = new LocationObject();
    location.latitude = event.coords.lat;
    location.longitude = event.coords.lng;
    location.selected = true;
    this.locations.push(location);

    this.distanceFromMe = this.locationService.getDistanceFromLatLonInKm(
      this.locations[0].latitude, this.locations[0].longitude,
      location.latitude, location.longitude)

    console.log('distance from me', this.distanceFromMe);

    this.locationCount++;
  }

  getCurrentLocation() {
    let location = this.locationService.getCurrentLocation();
    //console.log('location', location);
    location.selected = true;
    this.locations[0] = location;
    //this.locationCount++;
    //console.log('my location', location[0]);
  }

  // private getMyLocation() {
  //   this.myLocation = this.locationService.getCurrentLocation();
  //   console.log('myLocation', this.myLocation);
  // }

  private frame() {
    this.getCurrentLocation();
  }

  private getImage() {
    this.apiService.getImage()
    .subscribe(photo =>
      this.imageJsons.push(photo[0]) //.urls.raw + '&w=1500&dpi=2') // width + dpi
      //console.log(photo[0].urls.raw + '&w=1500&dpi=2')
    );
  }
}
